.home {
  position: relative;
  background-color: #ebebeb;

  &_title {
    text-align: center;
    margin: 40px 0;
    h2 {
      font-weight: 500;
      color: #474c5f;
      text-transform: uppercase;
    }
  }

  &_product,
  &_section1 {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    max-width: 1170px;
    margin: auto;
  }

  &_product1 {
    position: relative;
    margin: 0 10px 20px;
    background-color: #fff;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    border: 1px solid #ebebeb;
  }

  &_pos {
    position: absolute;
    left: 10px;
    margin-top: 10px;
    background-color: #f62d3e;
    color: #fff;
    width: 50px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-weight: 700;
    border-radius: 4px;
  }

  &_badge {
    position: relative;
    overflow: hidden;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;

    img {
      width: 270px;
      height: 200px;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;

      &:hover {
        transform: scale(1.2);
        cursor: pointer;
        transition: all 0.3s linear;
      }
    }
  }

  &_price {
    margin: 10px 0;
  }

  &_text {
    text-align: center;
    padding-bottom: 20px;

    p {
      font-size: 16px;
      &:hover {
        color: #f62d3e;
        cursor: pointer;
      }
    }

    button {
      color: #fff;
      background-color: #f62d3e;
      border: none;
      padding: 10px 14px;
      border-radius: 4px;

      &:hover {
        transition: all 0.3s linear;
        background-color: #c52432;
        cursor: pointer;
      }
    }
  }

  &_section {
    padding-bottom: 40px;
  }

  &_section1 {
    margin-top: 80px;
    justify-content: space-between;
  }

  &_inner {
    display: flex;
    padding-bottom: 20px;

    &__box {
      max-width: 260px;
      margin-left: 20px;
      font-size: 14px;

      p {
        color: #878a96;
        margin-top: 10px;
      }
    }
  }
}

.priceAmount {
  color: #858585;
  text-decoration: line-through;
  font-size: 14px;
}

.priceRed {
  color: #f62d3e;
  margin-left: 10px;
}

.home1 {
  position: relative;
}

//reponsive
@media screen and (min-width: 922px) and (max-width: 1200px) {
  .home {
    &_product {
      margin-left: 16px;
    }

    &_section1 {
      margin: 80px 30px 0 30px;
    }

    &_inner {
      padding-bottom: 60px;
    }
  }
}

@media screen and (min-width: 768px) and (max-width: 922px) {
  .home {
    &_product {
      margin-left: 16px;
    }

    &_inner {
      padding-bottom: 60px;

      img {
        width: 60px;
        height: 60px;
      }
    }
  }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
  .home {
    &_product {
      margin-left: 16px;
    }

    &_section1 {
      margin: 80px 30px 0 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .home {
    &_product,
    &_section1 {
      justify-content: center;
    }

    &_section1 {
      margin: 80px 20px 0 20px;
    }

    &_title {
      h2 {
        font-size: 14px;
      }
    }

    &_inner {
      padding-bottom: 40px;
      img {
        width: 60px;
        height: 60px;
      }
    }
  }
}
