.Footer {
  background-image: url(../Image/bg_footer.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.Footer1 {
  display: flex;
  justify-content: space-between;
  max-width: 1170px;
  margin: auto;
  padding: 30px 0;
  height: 400px;

  &_section {
    text-transform: uppercase;
    color: #fff;
    font-size: 20px;
    text-align: left;
    margin-bottom: 20px;
  }

  li {
    color: #acacac;
    list-style: none;
    text-align: left;
    margin-bottom: 10px;
  }

  &_span {
    margin-left: 8px;
  }

  &_i {
    color: #f62d3e;
  }

  &_icon {
    display: flex;
    margin-top: 20px;
  }

  &_iconbox {
    margin-left: 10px;
  }

  &_card {
    width: 100%;
    height: 100px;
  }
}

//reponsive
@media screen and (min-width: 922px) and (max-width: 1200px) {
  .Footer1 {
    margin: 0 40px;
  }
}

@media screen and (min-width: 768px) and (max-width: 922px) {
  .Footer1 {
    display: block;
    height: auto;
    margin-left: 40px;

    &_card {
      width: auto;
    }
  }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
  .Footer1 {
    display: block;
    height: auto;
    margin-left: 40px;

    &_card {
      width: auto;
    }
  }
}

@media screen and (max-width: 576px) {
  .Footer {
    display: flex;
  }

  .Footer1 {
    display: block;
    height: auto;
    margin: auto 20px;

    &_card {
      width: auto;
    }

    li {
      padding-left: 10px;
    }

    &_section {
      font-size: 16px;
    }
  }
}
