.icon {
  position: fixed;
  bottom: 50px;
  right: 50px;
  width: 50px;
  height: 50px;

  button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background-color: #62b35d;
    border-radius: 50%;
    border: none;
    cursor: pointer;
  }

  i {
    color: #fff;
  }

  &::before {
    content: "";
    position: absolute;
    background-color: #62b35d;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    z-index: -1;
    animation: waves 3s linear 0.5s infinite;
  }
}

@keyframes waves {
  0% {
    transform: scale(0.7);
    opacity: 0.2;
  }

  50% {
    transform: scale(1.5);
    opacity: 0.2;
  }

  100% {
    transform: scale(0.7);
    opacity: 0.2;
  }
}
